/*=== 2.1 BUTTON ===*/
.btn {
  font-family: 'Poppins', sans-serif;
  line-height: 14px;
  padding: 15px 0;
  color: #ffffff;
  font-weight: 500;
  border: none;
  text-transform: uppercase;
  background-color: transparent;
  border-radius: 4px;
  letter-spacing: 1px;
}

