  .btn {
    padding: 13px 0;
    font-size: 12px;
  }
}

@media (min-width: 992px) {
  .btn {
    padding: 15px 0;
    font-size: 14px;
  }
}

.btn:focus, .btn.active, .btn:active:focus, .btn:hover {
  border: none;
  outline: 0;
  color: #ffffff;
  box-shadow: none;
}

.waves-button, .waves-button:hover,
.waves-button:visited, .waves-button-input {
