/*=== 5.20 PRELOADER ===*/
.smooth-loader-wrapper {
  background-color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2000;
}

.smooth-loader-wrapper .smooth-loader {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2100;
  line-height: 0;
}

.smooth-loader .loader1 {
  position: relative;
  height: 100%;
  display: inline-block;
}

.smooth-loader .loader1 .loader-target {
  position: relative;
  height: 60px;
  width: 60px;
  margin-left: -30px;
  margin-top: -30px;
}

.loader-target .loader-target-main {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  transform: rotate(45deg) translateZ(0);
  background: #00C9FF;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2196f3, #00C9FF);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2196f3, #00C9FF);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  animation-name: qodefTargetRotate;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(0.38, 0.76, 0, 0.87);
  animation-delay: 0s;
  animation-fill-mode: forwards;
  outline: transparent solid 1px;
}

.loader-target:after {
  content: '';
  position: absolute;
  top: 5%;
  left: 5%;
  height: 90%;
  width: 90%;
  border-radius: 50%;
  background: #fff;
  animation-name: targetScale;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(0.38, 0.76, 0, 0.87);
  animation-delay: 0s;
  animation-fill-mode: forwards;
}

.loader-target .loader-target-inner {
  position: absolute;
  top: calc(50% - 20px);
  left: calc(50% - 20px);
  height: 40px;
  width: 40px;
  z-index: 100;
  opacity: 0;
  border-radius: 50%;
  background: #00C9FF;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2196f3, #00C9FF);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2196f3, #00C9FF);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  animation-name: targetScale;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(0.38, 0.76, 0, 0.67);
  animation-delay: 0s;
  animation-fill-mode: forwards;
}

.loader-target .loader-target-inner:after {
  content: '';
  position: absolute;
  top: 15px;
  left: 15px;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background: #fff;
  animation-name: qodefTargetScale;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(0.38, 0.96, 0, 0.37);
  animation-delay: 0s;
  animation-fill-mode: forwards;
}

/*=== 5.21 KEY FRAMES ANIMATION ===*/
@-webkit-keyframes targetScale {
  0% {
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    transform: scale(0);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}

.loaded #preloader {
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.7s ease-out;
  -moz-transition: all 0.7s ease-out;
  -ms-transition: all 0.7s ease-out;
  -o-transition: all 0.7s ease-out;
  transition: all 0.7s ease-out;
}

